
.about {
  width: 100%;
  height: 100%;
  background: #857578;
  padding: 50px;
}
.about .title {
  margin: 10px;
  font-size: 55px;
  text-shadow: 1px 2px #857578, 2px 4px #aee2d9;
  font-weight: 800;
  color: #fff;
}
.about .download {
  text-shadow: 1px 1px #857578, 2px 2px #aee2d9;
  font-size: 30px;
  color: #fff;
  text-decoration: none;
}
.about .download:hover {
  color: #839acc;
}
.about .body .title {
  margin-top: 50px;
  font-size: 50px;
}
.about .body ul:last-child {
  margin-top: 30px;
}
.about .body ul:last-child li {
  display: inline-block;
  margin: 10px;
}
.about .body ul .infos {
  list-style: none;
  color: rgba(255,255,255,0.5);
  font-size: 18px;
  line-height: 30px;
}
.about .body ul .infos .link {
  display: inline-block;
  text-decoration: none;
}
.about .body ul .infos .link .iconfont {
  padding: 0;
  font-size: 36px;
  color: rgba(255,255,255,0.6);
}
.about .body ul .infos .link .iconfont:hover {
  color: #839acc;
}


