
.project {
  width: 100%;
  height: 100%;
  background: #fd851c;
}
.project .title {
  margin: 30px;
  font-size: 60px;
  text-shadow: 1px 2px #fd851c, 2px 4px #aee2d9;
  font-weight: 800;
  color: #fff;
}
.project .body {
  height: 80%;
}
.project .body ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.project .body ul li {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  list-style: none;
  color: rgba(255,255,255,0.6);
}
.project .body ul li .project-name {
  font-size: 24px;
  color: rgba(255,255,255,0.8);
  text-decoration: none;
  line-height: 35px;
}
.project .body ul li .project-name:hover {
  color: #9f3;
}


